
import AlloyFinger from 'alloyfinger'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'

import { mapGetters } from 'vuex'
import menu from '../common/menu.vue'
export default {
data() {
return {
menuFlag:false,
menus:{
en:'English',
zh:'中文'
},
markFlag:false,
menuShow:false
};
},
created:function () {

localStorage.visit = 1;

localStorage.menuFlag = 'guide';

this.$store.dispatch('firstLanguage');

$(function(){
$(".navbar-center").css('marginLeft',0);
});
},
mounted(){
let _this = this;

var alloyTouch = new AlloyTouch({
touch:".guide_bottom",//反馈触摸的dom
vertical: true,//不必需，默认是true代表监听竖直方向touch

property: "translateY",  //被运动的属性
min: 100, //不必需,运动属性的最小值
max: 2000, //不必需,滚动属性的最大值
sensitivity: 1,//不必需,触摸区域的灵敏度，默认值为1，可以为负数
factor: 1,//不必需,表示触摸位移与被运动属性映射关系，默认值是1
step: 45,//用于校正到step的整数倍
bindSelf: false,
initialValue: 0,
change:function(value){  },
touchStart:function(evt, value){  },
touchMove:function(evt, value){  },
touchEnd:function(evt,value){
_this.$router.push('/unlogin')

},
tap:function(evt, value){  },
pressMove:function(evt, value){  },
animationEnd:function(value){  } //运动结束
})


},
methods: {

showSlide:function () {
this.menuShow = true;
if(this.menuFlag == false){
$('.guide').animate({
'left':'-5rem',
},500)
$('.mark').animate({
'left':'-5rem',
},500);
this.menuFlag = true;
this.markFlag = true;
}
},

goSearch:function () {

this.$router.push({path:'/search',query:{searchFlag:'guide'}});
},

hideSlide:function () {
if(this.menuFlag == true){
$('.guide').animate({
'left':0,
},500)
$('.mark').animate({
'left':0,
},500);
this.menuFlag = false;
this.markFlag = false;
}
},

goHome:function () {


},

goLogin:function () {
this.$router.push('/loginforguest')
},
hideAction:function (key) {

let params = {
token: localStorage.TOKEN,
platform: localStorage.platform,
identity:localStorage.identity,
lang:key

}
this.$store.dispatch('tabLanguage', params).then((res) => {
if(res.code == 0){

localStorage.LANGUAGE = key;
this.$store.dispatch('updateLanguage',key)

}else{
this.$dialog.toast({mes: res.msg, timeout: 1000});
}
})

},



},
components: {
'v-menu': menu,
Actionsheet
},
computed: {
...mapGetters([
'language',
'tabLanShow'
])
},
mounted:function () {

isHomePage(0)
}
};



